import * as styles from "../../styles/restaurant.module.css";
import React, { useEffect, useState } from "react";
import RestaurantHome from "./dumb/RestaurantHome";
import MenuHome from "./dumb/MenuHome";
import Loading from "./dumb/Loading";
import MediaQuery from "react-responsive";
import { useDispatch, useSelector } from "react-redux";
import { setInfoData, setHoursData, setMenuData, toggleView } from "../../state/menuSlice";
import Papa from "papaparse";
const config = require("../../../config");

const Menu = () => {
    const dispatch = useDispatch();
    const [loading, setLoading] = useState(true);
    const showMenu = useSelector((state) => state.restaurant.showMenu);

    const fetchSpreadsheetData = async (sheetName) => {
        const url = `https://docs.google.com/spreadsheets/d/${config.SPREADSHEET_ID}/gviz/tq?tqx=out:csv&sheet=${sheetName}`;

        try {
            const response = await fetch(url);
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            const csvText = await response.text();

            // Parse CSV data using PapaParse
            return new Promise((resolve, reject) => {
                Papa.parse(csvText, {
                    header: false,
                    skipEmptyLines: true,
                    complete: (result) => {
                        const data = result.data.slice(1);
                        resolve(data);
                    },
                    error: (error) => reject(error),
                });
            });
        } catch (error) {
            console.error(`Error fetching spreadsheet data from sheet "${sheetName}":`, error);
            return [];
        }
    };

    useEffect(() => {
        const fetchData = async () => {
            try {
                const [info, hours, menu] = await Promise.all([
                    fetchSpreadsheetData("info"),
                    fetchSpreadsheetData("hours"),
                    fetchSpreadsheetData("menu"),
                ]);

                dispatch(setInfoData(info));
                dispatch(setHoursData(hours));
                dispatch(setMenuData(menu));
            } catch (error) {
                console.error("Error fetching spreadsheet data:", error);
            } finally {
                setLoading(false);
            }
        };

        fetchData();
    }, [dispatch]);

    return (
        <div>
            {loading ? (
                <Loading />
            ) : (
                <div>
                    
                    <MainContent showMenu={showMenu} />
                </div>
            )}
        </div>
    );
};

const MainContent = ({ showMenu }) => {
    return (
        <div>
            <MediaQuery maxWidth={767}>
                <div className={styles.mobile_restaurant}>
                    {showMenu ? <MenuHome /> : <RestaurantHome />}
                </div>
            </MediaQuery>

            <MediaQuery minWidth={768} maxWidth={991}>
                <div className={styles.mobile_restaurant}>
                    {showMenu ? <MenuHome /> : <RestaurantHome />}
                </div>
            </MediaQuery>

            <MediaQuery minWidth={992}>
                <div className={styles.restaurant}>
                    {showMenu ? <MenuHome /> : <RestaurantHome />}
                </div>
            </MediaQuery>
        </div>
    );
};

export default Menu;
